          question: 'A system with n-layer protocol hierarchy has an h-byte header added at each layer. An application generates a message of length M bytes. What fraction of the bandwidth is filled with headers?',
          options: ['h*n / M+ h*n','M + h*n / h*n','H*n / (M+h) *n','Other'],
